<template>
  <div class="response-page">
    <div class="card">
      <div style="border-radius: 200px; height: 200px; width: 200px; background: #f8faf5; margin: 0 auto;">
        <i class="checkmark">✓</i>
      </div>
      <h1>Success</h1>
      <p>We received your purchase request;<br/> we'll be in touch shortly!</p>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import * as spinners from 'epic-spinners'
export default {
  components: {
    ...spinners,   
  },
  name: 'response-page',
  data () {
    return { 
      status:'',
      tx_ref:'',
      transaction_id:'', 
      userData: JSON.parse(localStorage.getItem('LoggedUser')),     
      token: JSON.parse(localStorage.getItem('Token')),     
    }
  },
  computed: {    
  }, 
  methods : {
    FinalPost () {
      
      var formdata = {
        status: this.status,
        tx_ref: this.tx_ref,
        transaction_id: this.transaction_id,
        user:this.userData.user_id
      }

      var self = this

      let config = {
        headers: {
          Authorization: this.userData.user_id+' '+this.token
        }
      }

      axios.post(`${this.$apiUrl}UpdateCardPayment`, formdata , config)
      .then(function (res) {
        
        if(self.status == 'successful'){              
          self.showToast(`Payment has been successfully processed`, {
            position: 'bottom-right',
            icon: "fa-check",
            duration: 2500,
          })
          //setTimeout( () => self.$router.go(-1), 2500);
        } else {
          self.showToast(`An Error occured while processing your payment please retry`, {
            position: 'bottom-right',
            icon: "fa-close",
            duration: 2500,
          })
        }                  
      })
      .catch(function (error) {
          console.log(error)
          self.loading = false 
      })
    },    
  },
  created () {  
    let thePath = {};
    thePath = this.$route.fullPath
    console.log(thePath); 

    this.status = this.$route.query.status
    this.tx_ref = this.$route.query.tx_ref 
    this.transaction_id = this.$route.query.transaction_id

    this.$router.replace({'query': null});

    this.FinalPost();

    console.log(this.status+','+this.tx_ref+','+this.transaction_id);  
  } 
} 
</script>
<style lang="scss">
  @import url('https://fonts.googleapis.com/css?family=Roboto+Condensed');

  .response-page {
    text-align: center;
    padding: 40px 0;
    background: #ebf0f5;
  }

  h1 {
    color: #88b04b;
    font-family: "Nunito Sans", "Helvetica Neue", sans-serif;
    font-weight: 900;
    font-size: 40px;
    margin-bottom: 10px;
  }

  p {
    color: #404f5e;
    font-family: "Nunito Sans", "Helvetica Neue", sans-serif;
    font-size: 20px;
    margin: 0;
  }

  i {
    color: #9abc66;
    font-size: 100px;
    line-height: 200px;
    margin-left: -15px;
  }

  .card {
    background: white;
    padding: 60px;
    border-radius: 4px;
    box-shadow: 0 2px 3px #c8d0d8;
    display: inline-block;
    margin: 0 auto;
  }

</style>
   
